const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list:[
				// {id:'1',name:'例会',img:'../static/card1.png'},
				// {id:'1',name:'例会d',img:'../static/card1.png'},
				// {id:'1',name:'例会的',img:'../static/card1.png'},
			],
			imgUrl: ''
		}
	},
	onLoad() {
		this.imgUrl = helper.imgUrl;
		this.getList()
	},
	
	methods: {
		// 获取数据
		getList(){
			uni.request({
				url: `${helper.requestUrl}/promote-teamlist`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res,'****');
					if (res.data.status_code == 200) {
						this.list = res.data.datas;
					} else {
						// uni.showToast({
						// 	title: res.data.message
						// });
					}
				}
			});
		},
		getMore(e){
			console.log(e)
			uni.navigateTo({
				url: `/pages/meTeamList?index=${e}`
			})
		},
		userDetail(i){
			console.log(i)
			
			let index = i.currentTarget.dataset.index
			let itemNew = JSON.stringify(i.currentTarget.dataset.item)
			uni.navigateTo({
				url:`/pages/teamPeopleDetail?itemDetail=${itemNew}&index=${index}`
			})
		}
	}
}
